import React, { Component } from 'react';
import { Mutation, Query } from 'react-apollo';
import gql from 'graphql-tag';
import Router from 'next/router';
import Form from './styles/Form';
import formatMoney from '../lib/formatMoney';
import Error from './ErrorMessage';

const SINGLE_LISTING_QUERY = gql`
  query SINGLE_LISTING_QUERY($id: ID!) {
    listing(where: { id: $id }) {
      address
      description
      rooms
      bath
      lotSize
      houseSize
      price
    }
  }
`;
const UPDATE_LISTING_MUTATION = gql`
  mutation UPDATE_LISTING_MUTATION(
    data: ListingUpdateInput!, 
    id: ID!
  ) {
    updateListing(
      data: {
        address: $address
        description: $description
        rooms: $rooms
        bath: $bath
        lotSize: $lotSize
        houseSize: $houseSize
        price: $price
      }
      
    ) {
      id
      title
      description
      price
    }
  }
`;

class UpdateItem extends Component {
  state = {};
  handleChange = e => {
    const { name, type, value } = e.target;
    const val = type === 'number' ? parseFloat(value) : value;
    this.setState({ [name]: val });
  };
  updateListing = async (e, updateListingMutation) => {
    e.preventDefault();
    console.log('Updating Item!!');
    console.log(this.state);
    const res = await updateListingMutation({
      variables: {
        id: this.props.id,
        ...this.state,
      },
    });
    console.log('Updated!!');
  };

  render() {
    return (
      <Query
        query={SINGLE_LISTING_QUERY}
        variables={{
          id: this.props.id,
        }}
      >
        {({ data, loading }) => {
          if (loading) return <p>Loading...</p>;
          if (!data.item) return <p>No Item Found for ID {this.props.id}</p>;
          return (
            <Mutation mutation={UPDATE_LISTING_MUTATION} variables={this.state}>
              {(updateListing, { loading, error }) => (
                <Form onSubmit={e => this.updateListing(e, updateListing)}>
                  <Error error={error} />
                  <fieldset disabled={loading} aria-busy={loading}>
                    <label htmlFor="address">
                      Address
                      <input
                        type="text"
                        id="address"
                        name="address"
                        placeholder="Address"
                        required
                        value={this.state.title}
                        onChange={this.handleChange}
                      />
                    </label>

                    <label htmlFor="description">
                      Description
                      <textarea
                        id="description"
                        name="description"
                        placeholder="Enter A Description"
                        required
                        value={this.state.description}
                        onChange={this.handleChange}
                      />
                    </label>

                    <label htmlFor="rooms">
                      Rooms
                      <input
                        type="number"
                        id="rooms"
                        name="rooms"
                        placeholder="Rooms"
                        required
                        value={this.state.rooms}
                        onChange={this.handleChange}
                      />
                    </label>

                    <label htmlFor="price">
                      Bath
                      <input
                        type="number"
                        id="bath"
                        name="bath"
                        placeholder="Bathrooms"
                        required
                        value={this.state.bath}
                        onChange={this.handleChange}
                      />
                    </label>

                    <label htmlFor="lotSize">
                      Lot Size
                      <input
                        type="number"
                        id="lotSize"
                        name="lotSize"
                        placeholder="Lot Size"
                        required
                        value={this.state.lotSize}
                        onChange={this.handleChange}
                      />
                    </label>
                    
                    <label htmlFor="houseSize">
                      House Size
                      <input
                        type="number"
                        id="houseSize"
                        name="houseSize"
                        placeholder="houseSize"
                        required
                        value={this.state.houseSize}
                        onChange={this.handleChange}
                      />
                    </label>

                    <label htmlFor="price">
                      Price
                      <input
                        type="number"
                        id="price"
                        name="price"
                        placeholder="Price"
                        required
                        value={this.state.price}
                        onChange={this.handleChange}
                      />
                    </label>
                    <button type="submit">Sav{loading ? 'ing' : 'e'} Changes</button>
                  </fieldset>
                </Form>
              )}
            </Mutation>
          );
        }}
      </Query>
    );
  }
}

export default UpdateItem;
export { UPDATE_LISTING_MUTATION };
